import React from 'react'

function WorldTotal(props){
  const { data, dict } = props
  let totalCases = 0
  let totalDeaths = 0
  let totalRecovered = 0
  
  data.forEach(country =>{
    totalCases += country.cases
    totalDeaths += country.deaths
    totalRecovered += country.recovered
  })
  
  const nfObject = new Intl.NumberFormat('en-US'); 
  totalCases = nfObject.format(totalCases);
  totalDeaths = nfObject.format(totalDeaths);
  totalRecovered = nfObject.format(totalRecovered);
  
  return(
    <div className="world-total" lang={ dict.lang }>
      <span lang={ dict.lang }>{ dict.cases }</span>
      <p className="world-total-number world-total-cases">{ totalCases }</p>
      <span lang={ dict.lang }>{ dict.deaths }</span>
      <p className="world-total-number world-total-deaths">{ totalDeaths }</p>
      <span lang={ dict.lang }>{ dict.recovered }</span>
      <p className="world-total-number world-total-recovered">{ totalRecovered }</p>
    </div>
  )
}

export default WorldTotal