import React from 'react'

function LanguageSwitch(props){
    let khmer_btn_class = "language-switch-khmer"
    let english_btn_class = "language-switch-english"
    if(props.isChecked){
        english_btn_class += " language-switch-english-active"
    }else{
        khmer_btn_class += " language-switch-khmer-active"
    }
    return(
        <div className="language-switch">
            <input name="langSwitch" onClick={ props.handleChange } type="button" className={ khmer_btn_class } value="Khmer" />
            <label className="switch">
                <input 
                    name="langSwitch"
                    type="checkbox" 
                    checked = { props.isChecked } 
                    onChange= { props.handleChange }
                />
                <span className="slider round"></span>
            </label>
            <input name="langSwitch" onClick={ props.handleChange } type="button" className={ english_btn_class } value="English" />
        </div>
    )

}

export default LanguageSwitch