import React, { useState, useEffect } from 'react'

import SearchBar from './SearchBar'
import ReportCard from './ReportCard'
import dataTest from '../source/data'
import WorldTotal from './WorldTotal'
import khEnDictionary from '../source/language'
import LanguageSwitch from './LanguageSwitch'

const _KHMER_DEBUG = true

function DailyReport(){
  const [state, setState] = useState({
    filteredText: "",
    isCambodia: false,
    isUSA: false,
    updatedOn: "",
    langSwitch: false
  })

  const [dict, setDict] = useState(khEnDictionary.languages.km.strings)
  const [data, setData] = useState([])

  const covidUrl = "https://corona.lmao.ninja/v2/countries"


  useEffect(()=>{
    if(_KHMER_DEBUG){
      const { updated } = dataTest[0]
        let d = new Date(updated)
        setState(prevState =>{
          return {...prevState, updatedOn: d.toLocaleDateString('en-US', { 
            weekday: 'long', 
            year: 'numeric', 
            month: 'long',
            day: 'numeric' })}
      })
      setData(dataTest)
    } else {
      fetch(covidUrl)
      .then(res => res.json())
      .then(resData => {
          setData(prevData =>{
              let newData = []
              resData.forEach(country =>{
                newData.push(country)
              })
              return newData
            })
      })
      .catch(error => {
        console.log("Error : " + error)
      })
    }
  }, [])
  
  useEffect(() =>{
    let date = ""
    let d = null
    if(data.length > 0){
      d = new Date(data[0].updated)
      date = d.toLocaleDateString('en-US', { 
        weekday: 'long', 
        year: 'numeric', 
        month: 'long',
        day: 'numeric' })
    }
    
    if(state.langSwitch){
      setDict(khEnDictionary.languages.en.strings)
    }else{
      setDict(khEnDictionary.languages.km.strings)
      date = toKhmerDate(date)
    }

    setState(prevState =>{
        return {...prevState, updatedOn: date}
    })
  }, [state.langSwitch, data])

  useEffect(() => {
    changeTitle(dict)
  }, [dict])

  const handleChange = (e)=>{
    const {name, type, value, checked} = e.target
    if(type === "button" && name === "langSwitch")
    {
        setState(prevState =>{
            return {...prevState, 
              [name]: value.toLowerCase() === "english" ? true : false}
      })
    } else if(type === "checkbox"){
      setState(prevState => {
        return {...prevState, [name]: checked}
      })
    } else if(type === "text"){
      setState(prevState =>{
        return {...prevState, [name]: value}
      })
    } else{
      setState(prevState =>{
        if(name === "clear"){
          return {...prevState, 
                  filteredText: "",
                  isCambodia: false,
                  isUSA: false
                }
        }else{
          let newValue = name === "isUSA" ? !prevState.isUSA : !prevState.isCambodia
          return {...prevState, [name] : newValue}
        }
      })
    }
  }
  
  return (
    <div className="daily-report">
      <LanguageSwitch 
        isChecked = { state.langSwitch }
        handleChange={ handleChange }
      />
      <header className="daily-report-title"><span lang={ dict.lang }>{dict.report_title}</span></header>
      <p className="daily-report-updated" lang={ dict.lang }>{state.updatedOn}</p>
      { dict.data_source }: <a 
        className="data-source" 
        href="https://corona.lmao.ninja/countries" 
        target="_blank"
        rel="noopener noreferrer"
        >Worldometer</a>
      <WorldTotal data={ data } dict={ dict }/>
      
      <SearchBar 
        filteredText={state.filteredText} 
        isCambodia={state.isCambodia}
        isUSA={state.isUSA}
        handleChange={handleChange}
        dict={ dict }
        />
      
      <ReportCard 
        filteredText={ state.filteredText } 
        isCambodia={ state.isCambodia }
        isUSA={ state.isUSA }
        data={ data }
        dict={ dict }
        />
    </div>
  )
}

function toKhmerDate(date){
  if(date === "")
    return date
  
  const { days, months, numbers} = khEnDictionary
  
  let dateStrings = date.split(",")

  const [day, monthDate, year] = dateStrings
  let md = monthDate.trim().split(" ")

  const khmerDay = days[day.toLowerCase()]
  let khmerDate = getKhmerNumber(md[1], numbers)
  const khmerMonth = months[md[0].toLowerCase()]
  let khmerYear = getKhmerNumber(year, numbers)
  
  return `${khmerDay}  ${khmerDate} ${khmerMonth}  ${khmerYear}` 
}

function getKhmerNumber(numStr, numbersMap){
  let num = ""
  for( let ch of numStr.trim())
  {
      num += numbersMap[ch]
  }
  return num
}

function changeTitle(dict){
  let title = document.getElementById("site-title")
  title.innerHTML = dict.report_title
}

export default DailyReport