import React from 'react'

function SearchBar(props){
  
  const { filteredText, isCambodia, isUSA, handleChange, dict } = props
  
  let cam_btn = isCambodia ? "cambodia cambodia-active" : "cambodia"
  let usa_btn = isUSA ? "usa usa-active" : "usa"

  return(
    <div className="searchbar">
      <div className="search-bar-input">
      <input 
        name="filteredText" 
        type="text" 
        value={ filteredText }
        onChange={ handleChange }
        placeholder={ dict.search }
        className="search-bar-textbox"
        />
      
      <button
        type="button"
        name="clear"
        onClick= { handleChange }
        className="search-bar-clear-button"
        lang={ dict.lang }
        >{ dict.clear_btn }
      </button>
      </div>
        
      <div className="search-bar-option" lang={ dict.lang }>
        { dict.instruction }
      </div>
      <div className="searchbar-button">       
        <div className="searchbar-button1">
          <button 
            type="button"
            name="isCambodia" 
            onClick={ handleChange }
            className= { cam_btn }
            > { dict.cambodia }
          </button>
        </div>
        <div className="searchbar-button2">
          <button 
            type="button"
            name="isUSA"
            onClick={ handleChange }
            className={ usa_btn }
            > { dict.usa }
          </button>
        </div>
        
      </div>
    </div>
  )
}

export default SearchBar