import React from 'react'

import ReportCardHeader from './ReportCardHeader'
import ReportCardBody from './ReportCardBody'

function ReportCard(props){
  const { filteredText, isCambodia, isUSA, data } = props
  let cards = []
  data.forEach(report =>{
    let isIncluded = false
    if(isCambodia || isUSA){
      if(report.country.toLowerCase() === "cambodia" && isCambodia)
        isIncluded = true
      if(report.country.toLowerCase() === "usa" && isUSA)
        isIncluded = true
      if(filteredText !== "" && report.country.toLowerCase().indexOf(filteredText.toLowerCase()) !== -1)
        isIncluded = true
    }else{
      if(report.country.toLowerCase().indexOf(filteredText.toLowerCase()) !== -1)
        isIncluded = true
    }
    
    if(isIncluded){
      cards.push(
        <div key={ report.country }className="report-card">
          <ReportCardHeader country = { report.country } />
          <ReportCardBody report={ report } dict= { props.dict } />
        </div>
      )
    }
  })
  return(
    <div className="report-cards">
      {cards}
    </div>
  )
}

export default ReportCard