import React from 'react'

function ReportCardBody(props){
  let { countryInfo, todayCases, todayDeaths, cases, deaths, recovered } = props.report
  const { flag } = countryInfo
  const { dict } = props

  const nfObject = new Intl.NumberFormat('en-US'); 
  todayCases = nfObject.format(todayCases);
  todayDeaths = nfObject.format(todayDeaths);
  cases = nfObject.format(cases);
  deaths = nfObject.format(deaths);
  recovered = nfObject.format(recovered);
  
  return (
    <div className="report-card-body">
      <div className="lists">
        <span lang={ dict.lang }>{ dict.today_cases }</span>
        <span className="list-amount">{todayCases}</span>
        <span>{ dict.unit }</span>
        
        <span className="end-section-today" lang={ dict.lang }>{ dict.today_deaths}</span>
        <span className="list-amount end-section-today">{todayDeaths}</span>
        <span className="end-section-today">{ dict.unit }</span>
        
        <span lang={ dict.lang }>{ dict.total_cases }</span>
        <span className="list-amount">{cases}</span>
        <span>{ dict.unit }</span>
        
        <span className="end-section-total" lang={ dict.lang }>{ dict.total_deaths}</span>
        <span className="list-amount end-section-total">{deaths}</span>
        <span className="end-section-total">{ dict.unit }</span>
        
        <span lang={ dict.lang }>{ dict.recovered }</span>
        <span className="list-amount">{recovered}</span>
        <span>{ dict.unit}</span>
        
      </div>
      <img className="report-card-body-flag" src={flag} alt="" />
    </div>
  )
}

export default ReportCardBody